import React, { Component } from 'react';
import TodoItemEdit from './TodoItemEdit';
import TodoItemView from './TodoItemView';
import './TodoItem.css';

class TodoItem extends Component {

  state = {
    mode: 'VIEW',
  }

  constructor(props) {
    super();
    this.state.value = props.value;
  }

  render() {
    let currentView;
    let classNames = `TodoItem ${this.props.done ? 'TodoItem--done' : ' '} ${this.props.important ? 'TodoItem--important' : ''}`;

    if (this.state.mode === 'VIEW') {
      currentView = <TodoItemView props={this.props} changeImportant={this.props.editTodo} changeMode={this.changeMode}/>;
    } else if (this.state.mode === 'EDIT') {
      currentView = <TodoItemEdit props={this.props} changeMode={this.changeMode}/>;
    }

    return (
      <li className={classNames}>
        {currentView}
      </li>
    );
  }

  changeMode = (mode) => {
    this.setState({ mode });
  }

}

export default TodoItem;