import React, {Component, Fragment} from 'react';

class TodoItemEdit extends Component {

  state = {
    value: ''
  }

  constructor(props) {
    super();
    this.state.value = props.props.value;
    this.textInput = React.createRef();
  }

  render() {
    return (
      <Fragment>
        <input className='TodoItem__input TodoItem__input--edit'
          ref={(input) => { this.textInput = input }}
          type='text'
          value={this.state.value}
          onKeyPress={this.handleKeyPress}
          onChange={this.handleEdit}
          onBlur={(e) => { this.handleDone(e) }}
        />
        <button className='TodoItem__button TodoItem__button--edit'
          onClick={(e) => { this.handleDone(e) }}>
            <img className='TodoItem__icon' src='svg/done.svg' alt='icon' />
        </button>
      </Fragment>
    )
  }

  componentDidMount() {
    this.setFocus();
  }
  
  setFocus = () => {
    this.textInput.focus();
  }

  handleEdit = (e) => {
    this.setState({
      value: e.target.value
    });
  }

  handleKeyPress = (e) => {
    if(e.key === 'Enter') {
      this.handleDone(e);
    }
  }
  
  handleDone = (e) => {
    const { id, done, important } = this.props.props;
    if(this.state.value) {
      this.props.changeMode('VIEW');
      this.props.props.editTodo(id, this.state.value, done, important);
    } else {
      alert('Пустое нельзя');
    }
  }

}

export default TodoItemEdit;
