import React, { Component, Fragment } from 'react';
import TodoItemControls from './TodoItemControls';

class TodoItemView extends Component {
  
  render() {
    const { changeMode, changeImportant } = this.props;
    const { id, value, done, important, deleteTodo, editTodo } = this.props.props;
    return (
      <Fragment>
        <div className="TodoItem__value" onClick={e => { editTodo(id, value, !done, important) }}>
          {value}
        </div>
        <TodoItemControls value={value} important={important} done={done} deleteTodo={deleteTodo} changeImportant={changeImportant} editTodo={() => { changeMode('EDIT') }} id={id} />
      </Fragment>
    )
  }
}

export default TodoItemView;